html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--bg-color);
  color: var(--content-black-color);
  font-family: Montserrat, sans-serif;
  position: relative;
}

h2, h3, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a, a:hover, a:focus {
  text-decoration: none;
}

.social-networks__list {
  width: 80px;
  justify-content: space-between;
  display: flex;
}

.registration-button {
  width: 145px;
  height: 40px;
  color: var(--white-color);
  background-color: var(--content-pink-color);
  filter: drop-shadow(10px 15px 25px rgba(232, 183, 183, .45));
  border: none;
  border-radius: 30px;
  font-size: 18px;
  font-weight: 400;
}

.registration-button a {
  color: var(--white-color);
}

:root {
  --content-black-color: #3c322d;
  --content-white-color: #f6f7f7;
  --content-pink-color: #f3848c;
  --white-color: #fff;
  --white-links-color: #fcfcfc;
  --bg-color: #e5e2e3;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.header {
  background-image: url("header-mobile.c349e831.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.header__container {
  width: calc(100% - 40px);
  margin-left: auto;
  margin-right: auto;
  padding: 0;
}

.header__navigation {
  justify-content: space-between;
  padding-top: 15px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.header__logo-picture {
  width: 108px;
}

.header__nav-menu {
  display: none;
}

.mobile-menu__button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.header__main-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header__title {
  letter-spacing: -.03em;
  margin-top: 20px;
  font-family: Montserrat Alternates;
  font-size: 28px;
  font-weight: 600;
  line-height: 34px;
}

.header__subtitle {
  font-family: Montserrat Alternates;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
}

.header__social-networks {
  margin-bottom: 8px;
}

@media screen and (min-width: 480px) {
  .header__container {
    width: 440px;
  }
}

@media screen and (min-device-pixel-ratio: 2) and (min-width: 320px), screen and (-webkit-min-device-pixel-ratio: 2) and (min-width: 320px), screen and (min-resolution: 192dpi) and (min-width: 320px), screen and (min-resolution: 2x) and (min-width: 320px) {
  .header {
    background-image: url("header-mobile@2x.4f325200.jpg");
  }
}

@media screen and (max-width: 767px) {
  .header__title {
    text-align: center;
    width: 280px;
  }

  .header__button {
    margin-top: 28px;
    margin-bottom: 28px;
  }
}

@media screen and (min-width: 768px) {
  .header {
    background-image: url("header-tablet.365c12ff.jpg");
  }

  .header__container {
    width: 704px;
  }

  .header__navigation {
    padding-top: 25px;
    padding-left: 32px;
