@media screen and (min-device-pixel-ratio: 2) and (min-width: 768px), screen and (-webkit-min-device-pixel-ratio: 2) and (min-width: 768px), screen and (min-resolution: 192dpi) and (min-width: 768px), screen and (min-resolution: 2x) and (min-width: 768px) {
  .header {
    background-image: url("header-tablet@2x.e4adbfbc.jpg");
  }
}

@media screen and (min-width: 1200px) {
  .header {
    width: 100%;
    background-image: url("header-desktop.b6171df8.jpg");
    background-position: center;
