  .header__nav-menu {
    display: flex;
  }

  .nav-menu__item {
    margin-right: 20px;
  }

  .nav-menu__item:hover .nav-menu__link, .nav-menu__item:focus .nav-menu__link {
    color: var(--white-links-color);
    background: linear-gradient(rgba(248, 110, 103, .63) 0%, rgba(253, 86, 143, .86) 100%);
    border-radius: 0;
    box-shadow: 0 7px 27px -2px rgba(253, 88, 140, .35);
  }

  .nav-menu__link {
    color: var(--content-black-color);
