    margin-bottom: 90px;
  }
}

@media screen and (min-device-pixel-ratio: 2) and (min-width: 1200px), screen and (-webkit-min-device-pixel-ratio: 2) and (min-width: 1200px), screen and (min-resolution: 192dpi) and (min-width: 1200px), screen and (min-resolution: 2x) and (min-width: 1200px) {
  .header {
    background-image: url("header-desktop@2x.18b6738a.jpg");
  }
}

